"use client"

import React, { useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import PickupLocationMap from "./pickup-location-map"

interface LocationPickerProps {
  value: string
  onChange: (value: string) => void
  apiKey: string
}

export function LocationPicker({ value, onChange, apiKey }: LocationPickerProps) {
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null)
  const [address, setAddress] = useState(value)

  const handleLocationChange = async (newLocation: { lat: number; lng: number }) => {
    setLocation(newLocation)
    try {
      // Use Google Maps Geocoding API to get address from coordinates
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?latlng=${newLocation.lat},${newLocation.lng}&key=${apiKey}`
      )
      const data = await response.json()
      if (data.results && data.results[0]) {
        const newAddress = data.results[0].formatted_address
        setAddress(newAddress)
        onChange(newAddress)
      }
    } catch (error) {
      console.error('Error getting address:', error)
    }
  }

  const handleAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newAddress = e.target.value
    setAddress(newAddress)
    onChange(newAddress)
  }

  const handleAddressSubmit = async () => {
    if (!address) return

    try {
      // Use Google Maps Geocoding API to get coordinates from address
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(address)}&key=${apiKey}`
      )
      const data = await response.json()
      if (data.results && data.results[0] && data.results[0].geometry) {
        const { lat, lng } = data.results[0].geometry.location
        setLocation({ lat, lng })
      }
    } catch (error) {
      console.error('Error getting coordinates:', error)
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex gap-2">
          <Input
            type="text"
            placeholder="Enter jobsite location"
            value={address}
            onChange={handleAddressChange}
            className="flex-1"
          />
        <Button 
          type="button" 
          variant="outline"
          onClick={handleAddressSubmit}
        >
          Search
        </Button>
      </div>
      <PickupLocationMap
        apiKey={apiKey}
        location={location}
        onLocationChange={handleLocationChange}
      />
    </div>
  )
}
